import styled from 'styled-components';

const HorizontalList = styled.ul`
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    margin: 0;
    width: 80%;
`;

export default HorizontalList;


